'use client';

import { useEffect, useState } from 'react';
import WalletConnect from '@/components/WalletConnect';
import Link from 'next/link';
import { initSDK } from '@/lib/sdk';
import { appConfig } from '@/lib/config';

// Sample content data
const sampleContent = [
  {
    id: '1',
    title: 'Sample Video Content',
    type: 'video',
    pricePerMinute: 0.02,
    description: 'Watch this sample video content',
  },
  {
    id: '2',
    title: 'Sample Audio Content',
    type: 'audio',
    pricePerMinute: 0.015,
    description: 'Listen to this sample audio content',
  },
  {
    id: '3',
    title: 'Sample Learning Content',
    type: 'learn',
    pricePerMinute: 0.025,
    description: 'Learn from this sample content',
  },
];

export default function Home() {
  const [sdkInitialized, setSdkInitialized] = useState(false);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);

  useEffect(() => {
    // Initialize SDK
    initSDK({
      network: appConfig.network,
      apiUrl: appConfig.apiUrl,
      apiKey: appConfig.apiKey,
      providerAddr: appConfig.providerAddr,
      platformAddr: appConfig.platformAddr,
      platformFeePct: appConfig.platformFeePct,
      asaId: appConfig.asaId,
      appId: appConfig.appId,
    }).then(() => {
      setSdkInitialized(true);
    });
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-cyan-50 dark:from-zinc-900 dark:to-zinc-800">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <header className="mb-8">
          <h1 className="mb-4 text-4xl font-bold text-zinc-900 dark:text-zinc-100">
            Play and Pay
          </h1>
          <p className="text-lg text-zinc-600 dark:text-zinc-400">
            Pay-as-you-watch micro-payments for digital content
          </p>
        </header>

        {/* Wallet Connection */}
        <div className="mb-8">
          <h2 className="mb-4 text-xl font-semibold text-zinc-900 dark:text-zinc-100">
            Wallet Connection
          </h2>
          <WalletConnect
            onConnect={(address) => setWalletAddress(address)}
            onDisconnect={() => setWalletAddress(null)}
          />
        </div>

        {/* Content Selection */}
        {walletAddress && (
          <div className="mb-8">
            <h2 className="mb-4 text-xl font-semibold text-zinc-900 dark:text-zinc-100">
              Available Content
            </h2>
            <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3">
              {sampleContent.map((content) => (
                <div
                  key={content.id}
                  className="rounded-lg border border-zinc-200 bg-white p-6 shadow-sm transition-shadow hover:shadow-md dark:border-zinc-800 dark:bg-zinc-900"
                >
                  <div className="mb-2 flex items-center justify-between">
                    <span className="rounded-full bg-blue-100 px-3 py-1 text-xs font-medium text-blue-800 dark:bg-blue-900/20 dark:text-blue-300">
                      {content.type}
                    </span>
                    <span className="text-sm font-semibold text-zinc-900 dark:text-zinc-100">
                      â‚¬{content.pricePerMinute.toFixed(2)}/min
                    </span>
                  </div>
                  <h3 className="mb-2 text-lg font-semibold text-zinc-900 dark:text-zinc-100">
                    {content.title}
                  </h3>
                  <p className="mb-4 text-sm text-zinc-600 dark:text-zinc-400">
                    {content.description}
                  </p>
                  <Link
                    href={`/player?contentId=${content.id}&title=${encodeURIComponent(content.title)}&price=${content.pricePerMinute}`}
                    className="block w-full rounded-lg bg-blue-500 px-4 py-2 text-center text-sm font-medium text-white transition-colors hover:bg-blue-600"
                  >
                    Start Now
                  </Link>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Info */}
        {!walletAddress && (
          <div className="rounded-lg border border-blue-200 bg-blue-50 p-4 dark:border-blue-800 dark:bg-blue-900/20">
            <p className="text-sm text-blue-800 dark:text-blue-200">
              Please connect your Pera Wallet to start watching content.
            </p>
          </div>
        )}
      </div>
    </div>
  );
}
